.header {
  position: absolute;
  width: 100%;
  padding: 52px 0px;
  padding-bottom: 0px;
  z-index: 101;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  top: 0px;
  /*------------------*/
  /*--- ATTRIBUTES ---*/
  /*------------------*/
  /*----------------*/
  /*--- ELEMENTS ---*/
