.header.fixed {
  position: fixed;
  padding: 22px 0px;
  top: 0px;
  transform: translateY(0px);
  left: 0px;
  background: #fff;
}
.header.fixed.show-menu {
  display: none;
}
.header.fixed:not(.visible):not(.force-show) {
  transform: translateY(-125px);
}
.header.fixed.force-hide {
  transform: translateY(-125px);
}
.header.fixed.visible {
  transform: translateY(0px);
}
.header.fixed .getTouch {
  top: 45px;
}
.header.fixed .hmbrg {
  top: 45px;
}
.header.fixed .icons {
  top: 45px;
}
.header.show-menu {
  position: fixed;
  background: transparent !important;
}
.header.show-menu .hmbrg span {
  background: #fff;
}
.header.show-menu .logo {
  opacity: 0;
}
.header.show-menu .item {
  opacity: 0;
}
.header.show-menu .getTouch {
  transform: translateX(101vw);
  opacity: 0;
}
.header.show-menu .icons {
  opacity: 1;
}
.header.h-login {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.header.open-search {
  filter: blur(1.5rem);
}
.header .col {
  position: relative;
}
.header .contents {
  display: flex;
  align-items: center;
}
.header .h-search {
  position: absolute;
  width: 100%;
  top: 0px;
  display: none;
  z-index: 12;
}
.header .logo {
  width: 88px;
  height: 44px;
  position: relative;
  cursor: pointer;
  margin-right: 40px;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .logo .svg {
  background: url('../img/all/logo.svg') no-repeat center center;
  background-size: contain;
  position: absolute;
  width: 100%;
  height: 100%;
  top: -10px;
}
.header .icons {
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  right: 3.5%;
  top: 75px;
  z-index: 10;
}
.header .icons .ico {
  position: relative;
  width: 45px;
  height: 45px;
  margin-left: 10px;
}
.header .icons .ico.ico-search {
  background: url('../img/all/ico-search.svg') no-repeat center center;
  background-size: contain;
}
.header .icons .ico.ico-user {
  background: url('../img/all/ico-login.svg') no-repeat center center;
  background-size: contain;
}
.header .hmbrg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 10px;
  display: block;
  cursor: pointer;
  left: 3.5%;
  top: 75px;
}
.header .hmbrg:hover span {
  width: 80%;
}
.header .hmbrg:hover span:nth-child(1) {
  width: 60%;
}
.header .hmbrg.active span {
  width: 100%;
  transform: rotate(45deg) translate(3px, 1px);
}
.header .hmbrg.active span:nth-child(1) {
  width: 100%;
  transform: rotate(-45deg) translate(5px, -4px);
}
.header .hmbrg span {
  position: absolute;
  width: 100%;
  height: 1px;
  top: 0px;
  display: block;
  left: 0px;
  background: #000000;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .hmbrg span:nth-child(1) {
  bottom: 0px;
  top: auto;
}
.header .go-back {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  display: block;
  cursor: pointer;
  left: 3.5%;
  top: 75px;
}
.header .go-back .svg {
  width: 100%;
  height: 100%;
  background: url('../img/all/ico-arrow-login.svg') no-repeat center center;
  background-size: contain;
}
.header .items {
  display: flex;
}
.header .item {
  color: #808080;
  font-family: gesta, sans-serif;
  font-weight: 300;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  margin-right: 30px;
  letter-spacing: 1px;
  cursor: pointer;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
