  font-weight: 400;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
}
.header .getTouch {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 46px;
  width: 130px;
  display: table;
  right: 3.5%;
  cursor: pointer;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  z-index: 11;
  top: 75px;
}
.header .getTouch:hover .over {
  clip-path: polygon(0% 0%, 100% 0, 100% 100%, 0% 100%);
}
.header .getTouch span {
  font-family: gesta, sans-serif;
  font-weight: 400;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  color: #000000;
  width: 100%;
  font-size: 16px;
  line-height: 24px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  text-align: center;
}
.header .getTouch .over {
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 66px;
  clip-path: polygon(50% 0%, 100% 0, 100% 100%, 100% 100%);
  background: #EF4050;
  position: absolute;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .getTouch .over span {
  position: absolute;
  line-height: 24px;
  color: #fff;
}
@media (max-width: 850px) {
  .header {
    padding: 30px 0px;
