  }
  .header .items {
    display: none;
  }
  .header .logo {
    margin-left: 0px;
    margin-right: 0px;
    width: 68px;
    height: 34px;
  }
  .header .logo .svg {
    top: -4px;
  }
  .header .getTouch {
    width: 110px;
    top: 50px;
  }
  .header .getTouch .over {
    height: 56px;
  }
  .header .hmbrg {
    top: 50px;
  }
  .header .icons {
    top: 50px;
  }
  .header .icons .ico {
    width: 35px;
    height: 35px;
  }
  .header .col {
    padding: 0px;
