@media (max-width: 425px) {
  .header.h-login {
    padding: 25px 0;
  }
  .header.h-login .logo .svg {
    top: -5px;
  }
  .header .logo .svg {
    width: 80%;
    top: 0px;
  }
  .header .col {
    width: 65%;
  }
  .header .hmbrg {
    left: 20px;
  }
  .header .icons {
    right: 20px;
  }
  .header .getTouch {
    right: 20px;
    height: 40px;
  }
  .header .getTouch span {
    font-size: 12px;
  }
  .header .getTouch .over {
    height: 40px;
    clip-path: polygon(64% 0, 100% 0, 100% 100%, 100% 100%);
  }
